import isEqual from "lodash/isEqual"
import {
  SourceSelectionInitialStateFull,
  SourceSelectorSelectedPackageInfo,
  SourceSelectorSelectedSourceInfo,
} from "../sourceSelector.types"

type GetIsSourceSelectorPristineProps = {
  initialStateFull: SourceSelectionInitialStateFull | null
  isFilterInclude: boolean
  selectedAllPackages: Array<SourceSelectorSelectedPackageInfo>
  selectedSources: Array<SourceSelectorSelectedSourceInfo>
}

function getIsSourceSelectorPristine({
  initialStateFull,
  isFilterInclude,
  selectedAllPackages,
  selectedSources,
}: GetIsSourceSelectorPristineProps): boolean {
  return (
    !!initialStateFull &&
    isFilterInclude === initialStateFull.isFilterInclude &&
    isEqual(
      selectedAllPackages.map(selectedPackage => selectedPackage.id),
      initialStateFull.packages.map(pkg => pkg.id),
    ) &&
    isEqual(
      selectedSources.map(selectedSource => selectedSource.id),
      initialStateFull.sources.map(source => source.id),
    )
  )
}

export { getIsSourceSelectorPristine }
